
const request = function (method, url, data, callback) {
    try {
        fetch(url, {
            method: method,
            body: JSON.stringify(data),
            headers: {'Content-Type': 'application/json'}
        }).then((response) => {
            return response.json();
        }).then((data) => {
            callback(data);
        });
    } catch (error) {
        console.error('error:', error);
    }
};

export default request;