import Component from "animate.js/src/components/Component";
import Clip from "animate.js/src/components/Clip";
import createElement from "animate.js/src/static/createElement";

const template = `
<div class="pl-1 pt-1">
    <div data-node="description">...</div>
</div>
`;

const DescriptionComponent = Component({
    template: template,
    parent: Clip('#app'),
    init(){},
    complete(){},
    render(){
        this.parent.append(this.template);
    },
    clear(){
        this.elements('node', 'description').textContent = '';
    },
    descriptions(data){
        if (data) {
            Object.keys(data).forEach((key) => {
                if(['action'].includes(key)) return;

                this.appendTitle(key);
                if (key === 'user') {
                    this.appendItem(data[key]);
                } else {
                    this.appendArrayItems(data[key]);
                }
            });
        }
    },
    appendArrayItems(data){
        Object.keys(data).forEach((key) => {
            this.appendItem(data[key]);
        });
    },
    appendItem(data){
        const div = createElement('pre', {}, JSON.stringify(data, null, 4))
        this.elements('node', 'description').appendChild(div);
    },
    appendTitle(title){
        const div = createElement('h4', {class: 'pt-1'}, title.toUpperCase())
        this.elements('node', 'description').appendChild(div);
    },
});

export default DescriptionComponent;
