import Component from "animate.js/src/components/Component";
import Clip from "animate.js/src/components/Clip";
import on from "animate.js/src/static/on";
import query from "animate.js/src/static/query";
import request from "../request";
import DescriptionComponent from "./DescriptionComponent";
import DashboardComponent from "./DashboardComponent";

const template = `
<div class="FromComponent pl-1 pt-1">
    <div>
        <input data-field="email" value="o.knysh@boosteroid.com" type="text">
        <button data-button="email">find by email</button>
        <button data-button="clear">clear result</button>
    </div>
</div>
`;

const FromComponent = Component({
    template: template,
    parent: Clip('#app'),
    state: {
        url: 'http://localhost:8010/demo/subscribe/back/index.php',
        email: '',
    },
    init() {},
    complete() {},
    render() {
        this.parent.append(this.template);

        on('[data-button="clear"]', 'click', (e) => {
            DescriptionComponent.clear();
        });

        on('[data-button="email"]', 'click', (e) => {
            DescriptionComponent.clear();

            const email = query('[data-field="email"]').value;
            const data = {
                action: 'info',
                email: email,
                host: DashboardComponent.stage.host,
            };
            request('post', this.state.url, data, (data) => {
                DescriptionComponent.descriptions(data);
            })
        })
    },
});

export default FromComponent;
