import Component from "animate.js/src/components/Component";
import Clip from "animate.js/src/components/Clip";
import createElement from "animate.js/src/static/createElement";
import FromComponent from "./FromComponent";

const template = `
<div class="DashboardComponent pl-1 pt-1">
    <select data-on="host">
        <option value="localhost" selected>localhost</option>
        <option value="172.16.0.51">stage.cloud.boosteroid.com</option>
    </select>
</div>
`;

const DashboardComponent = Component({
    template: template,
    parent: Clip('#app'),
    stage: {
        host: 'localhost'
    },
    init(){},
    complete(){},
    render(){
        this.parent.append(this.template);

        this.elements('on', 'host').addEventListener('change', (e) => {
            this.stage.host = e.target.value;
        });
    },
    appendItem(data){
        const div = createElement('pre', {}, JSON.stringify(data, null, 4))
        this.elements('node', 'description').appendChild(div);
    },
});

export default DashboardComponent;
